import React from 'react';
import { Typography, Grid, Table, TableHead, TableBody, TableRow, TableCell, Button, Container, Breadcrumbs } from '@mui/material';
import { Link } from 'react-router-dom';

function LeaveRequest() {
    // Dummy leave request data for demonstration
    const leaveRequests = [
        { id: 1, leaveType: 'Vacation', startDate: '2024-05-01', endDate: '2024-05-05', accepted: true },
        { id: 2, leaveType: 'Sick Leave', startDate: '2024-06-15', endDate: '2024-06-16', accepted: false },
        // Add more leave request data as needed
    ];

    // Function to handle canceling leave (simulated API call)
    const handleCancelLeave = async (requestId) => {
        try {
            // Simulated API call to cancel leave
            const response = await fetch(`https://api.example.com/leave/${requestId}`, {
                method: 'DELETE',
                headers: {
                    'Content-Type': 'application/json',
                    // Add any necessary authentication headers here
                },
            });
            if (!response.ok) {
                throw new Error('Failed to cancel leave');
            }
            // Handle success (e.g., show confirmation message)
            console.log('Leave canceled successfully');
        } catch (error) {
            console.error('Error canceling leave:', error);
            // Handle error (e.g., display error message)
        }
    };

    return (
        <Container>
            <Typography variant="h4" gutterBottom>
                Leave Request
            </Typography>
            <Breadcrumbs aria-label="breadcrumb">
                <Link underline="hover" color="inherit" to="/staff">
                    Staff Portal
                </Link>
                <Typography color="text.primary">Leave Request</Typography>
            </Breadcrumbs>

            {/* Table for leave requests */}
            <Table>
                <TableHead>
                    <TableRow>
                        <TableCell>Leave Type</TableCell>
                        <TableCell>Start Date</TableCell>
                        <TableCell>End Date</TableCell>
                        <TableCell>Accepted</TableCell>
                    </TableRow>
                </TableHead>
                <TableBody>
                    {leaveRequests.map((request) => (
                        <TableRow key={request.id}>
                            <TableCell>{request.leaveType}</TableCell>
                            <TableCell>{request.startDate}</TableCell>
                            <TableCell>{request.endDate}</TableCell>
                            <TableCell>{request.accepted ? 'Yes' : 'No'}</TableCell>
                        </TableRow>
                    ))}
                </TableBody>
            </Table>

            {/* Grid for buttons */}
            <Grid container spacing={2} style={{ marginTop: '20px' }}>
                <Grid item>
                    <Button component={Link} to="./new" variant="contained" color="primary">
                        New Request
                    </Button>
                </Grid>
                <Grid item>
                    <Button component={Link} to="./edit" variant="contained" color="primary">
                        Edit Request
                    </Button>
                </Grid>
                <Grid item>
                    <Button
                        variant="contained"
                        color="secondary"
                        onClick={() => handleCancelLeave(leaveRequests[0].id)} // Example: Cancel first request
                    >
                        Cancel Leave
                    </Button>
                </Grid>
            </Grid>
        </Container>
    );
}

export default LeaveRequest;
