import React, { useState, useEffect } from 'react';
import { Breadcrumbs, Typography, Container, Table, TableHead, TableBody, TableRow, TableCell, Button, Grid } from '@mui/material';
import { Link } from 'react-router-dom';

function CurrentRoster() {
    const [rosterData, setRosterData] = useState([]);
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        const fetchData = async () => {
            try {
                const response = await fetch('https://api.example.com/roster');
                if (!response.ok) {
                    throw new Error('Failed to fetch roster data');
                }
                const data = await response.json();
                setRosterData(data); // Update state with fetched data
                setLoading(false); // Set loading state to false
            } catch (error) {
                console.error('Error fetching roster data:', error);
                setLoading(false); // Set loading state to false in case of error
            }
        };

        // fetchData();
    }, []); // Run effect only once on component mount

    const renderTableHead = () => {
        return (
            <TableHead>
                <TableRow>
                    {/* Render table headers dynamically based on locations */}
                    {Array.from({ length: 6 }, (_, index) => (
                        <TableCell key={index}>Location {index + 1}</TableCell>
                    ))}
                </TableRow>
            </TableHead>
        );
    };

    const renderTableBody = () => {
        return (
            <TableBody>
                {rosterData.map((row) => (
                    <TableRow key={row.id}>
                        {/* Render table cells for each location in the row */}
                        {Array.from({ length: 6 }, (_, cellIndex) => (
                            <TableCell key={cellIndex}>{row[`location${cellIndex + 1}`]}</TableCell>
                        ))}
                    </TableRow>
                ))}
            </TableBody>
        );
    };

    return (
        <Container maxWidth="lg">
            <Breadcrumbs aria-label="breadcrumb" mt={3}>
                <Link underline="hover" color="inherit" to="/manage">
                    Manager Portal
                </Link>
                <Link underline="hover" color="inherit" to="/manage/roster">
                    Fortnightly Rosters
                </Link>
                <Typography color="text.primary">Current Roster</Typography>
            </Breadcrumbs>

            {/* Render the dynamic table */}
            <Table>
                {renderTableHead()}
                {loading ? (
                    <TableBody>
                        <TableRow>
                            <TableCell colSpan={6} align="center">
                                Loading...
                            </TableCell>
                        </TableRow>
                    </TableBody>
                ) : (
                    renderTableBody()
                )}
            </Table>

            {/* Render buttons below the table */}
            <Grid container spacing={2} style={{ marginTop: '20px' }}>
                <Grid item>
                    <Button component={Link} to="./amend" variant="contained" color="primary">
                        Amend
                    </Button>
                </Grid>
                <Grid item>
                    <Button component={Link} to="/manage/roster" variant="contained" color="secondary">
                        Close
                    </Button>
                </Grid>
            </Grid>
        </Container>
    );
}

export default CurrentRoster;
