import React, { useState, useEffect } from 'react';
import { Breadcrumbs, Typography, Container, Table, TableHead, TableBody, TableRow, TableCell, Button, Grid, Card, CardContent, Checkbox } from '@mui/material';
import { Link } from 'react-router-dom';

function AmendCurrentRoster() {
    const [rosterData, setRosterData] = useState([]);
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        const fetchData = async () => {
            try {
                const response = await fetch('https://api.example.com/roster');
                if (!response.ok) {
                    throw new Error('Failed to fetch roster data');
                }
                const data = await response.json();
                setRosterData(data); // Update state with fetched data
                setLoading(false); // Set loading state to false
            } catch (error) {
                console.error('Error fetching roster data:', error);
                setLoading(false); // Set loading state to false in case of error
            }
        };

        // Uncomment the below line to fetch data when the component mounts
        // fetchData();
    }, []); // Run effect only once on component mount

    const handleCheckboxChange = (locationIndex) => (event) => {
        const updatedRosterData = [...rosterData];
        updatedRosterData.forEach((row) => {
            row[`location${locationIndex + 1}`] = event.target.checked;
        });
        setRosterData(updatedRosterData);
    };

    const renderTableHead = () => {
        return (
            <TableHead>
                <TableRow>
                    {/* Render table headers dynamically based on locations */}
                    {Array.from({ length: 6 }, (_, index) => (
                        <TableCell key={index}>Location {index + 1}</TableCell>
                    ))}
                </TableRow>
            </TableHead>
        );
    };

    const renderTableBody = () => {
        return (
            <TableBody>
                {rosterData.map((row, rowIndex) => (
                    <TableRow key={row.id}>
                        {/* Render table cells and checkboxes for each location in the row */}
                        {Array.from({ length: 6 }, (_, cellIndex) => (
                            <TableCell key={cellIndex}>
                                <Checkbox
                                    checked={row[`location${cellIndex + 1}`]}
                                    onChange={handleCheckboxChange(cellIndex)}
                                />
                                {row[`location${cellIndex + 1}`]}
                            </TableCell>
                        ))}
                    </TableRow>
                ))}
            </TableBody>
        );
    };

    return (
        <Container maxWidth="lg">
            <Breadcrumbs aria-label="breadcrumb" mt={3}>
                <Link underline="hover" color="inherit" to="/manage">
                    Manager Portal
                </Link>
                <Link underline="hover" color="inherit" to="/manage/roster">
                    Fortnightly Rosters
                </Link>
                <Link underline="hover" color="inherit" to="/manage/roster/current">
                    Current Roster
                </Link>
                <Typography color="text.primary">Amend</Typography>
            </Breadcrumbs>

            <Grid container spacing={3} style={{ marginTop: '20px' }}>
                {/* Grid for the table (9 units) */}
                <Grid item xs={12} md={9}>
                    <Table>
                        {renderTableHead()}
                        {loading ? (
                            <TableBody>
                                <TableRow>
                                    <TableCell colSpan={6} align="center">
                                        Loading...
                                    </TableCell>
                                </TableRow>
                            </TableBody>
                        ) : (
                            renderTableBody()
                        )}
                    </Table>
                </Grid>

                {/* Grid for the card (3 units) */}
                <Grid item xs={12} md={3}>
                    <Card>
                        <CardContent>
                            <Typography variant="h6" gutterBottom>
                                Locations
                            </Typography>
                            <Grid container spacing={1}>
                                {Array.from({ length: 6 }, (_, index) => (
                                    <Grid key={index} item xs={6}>
                                        <Checkbox
                                            checked={false} // You can set default checked state if needed
                                            onChange={handleCheckboxChange(index)}
                                        />
                                        <Typography variant="body1">
                                            Location {index + 1}
                                        </Typography>
                                    </Grid>
                                ))}
                            </Grid>
                        </CardContent>
                    </Card>
                </Grid>
            </Grid>

            {/* Render buttons below the table */}
            <Grid container spacing={2} style={{ marginTop: '20px' }}>
                <Grid item>
                    <Button component={Link} to="/manage/roster/current" variant="contained" color="primary">
                        Close
                    </Button>
                </Grid>
                <Grid item>
                    <Button component={Link} to="" variant="contained" color="secondary">
                        Check
                    </Button>
                </Grid>
                <Grid item>
                    <Button component={Link} to="" variant="contained" color="success">
                        Publish
                    </Button>
                </Grid>
            </Grid>

        </Container>
    );
}

export default AmendCurrentRoster;
