import './App.css';
// @mui material components
import CssBaseline from "@mui/material/CssBaseline";
import { BrowserRouter, Route, Routes } from 'react-router-dom';
import SignIn from './components/SignIn';
import Staff from './components/Staff';
import Manager from './components/Manager';
import StaffRoster from './components/StaffRoster';
import ManagerRoster from './components/ManagerRoster';
import CurrentRoster from './components/CurrentRoster';
import AmendCurrentRoster from './components/AmendCurrentRoster';
import NewRoster from './components/NewRoster';
import DraftRoster from './components/DraftRoster';
