import React, { useState, useEffect } from 'react';
import { Breadcrumbs, Container, Typography, TextField, Grid, Button, Table, TableHead, TableBody, TableRow, TableCell } from '@mui/material';
import { Link } from 'react-router-dom';

function StaffRoster() {
    const [rosterData, setRosterData] = useState([]);
    const [loading, setLoading] = useState(true);
    const [searchQuery, setSearchQuery] = useState('');

    const handleSearchChange = (event) => {
        setSearchQuery(event.target.value);
    };

    const handleSearchSubmit = (event) => {
        event.preventDefault();
        // Perform search or submit action based on searchQuery
        console.log('Search Query:', searchQuery);
        // Add your custom logic here (e.g., filtering rosters)
    };

    useEffect(() => {
        // Fetch roster data from API (replace with actual API call)
        const fetchData = async () => {
            // Simulating fetching data from an API endpoint
            const response = await fetch('');
            const data = await response.json();
            setRosterData(data); // Update state with fetched data
            setLoading(false); // Set loading state to false
        };

        //fetchData();
    }, []); // Run effect only once on component mount

    const renderTableHead = () => {
        return (
            <TableHead>
                <TableRow>
                    {/* Render table headers dynamically based on locations */}
                    {Array.from({ length: 6 }, (_, index) => (
                        <TableCell key={index}>Location {index + 1}</TableCell>
                    ))}
                </TableRow>
            </TableHead>
        );
    };

    const renderTableBody = () => {
        return (
            <TableBody>
                {rosterData.map((row, rowIndex) => (
                    <TableRow key={rowIndex}>
                        {/* Render table cells for each location in the row */}
                        {Array.from({ length: 6 }, (_, cellIndex) => (
                            <TableCell key={cellIndex}>{row[`location${cellIndex + 1}`]}</TableCell>
                        ))}
                    </TableRow>
                ))}
            </TableBody>
        );
    };

    return (
        <Container maxWidth="lg">
            <Breadcrumbs aria-label="breadcrumb">
                <Link underline="hover" color="inherit" to="/staff">
                    Staff Portal
                </Link>
                <Typography color="text.primary">Fortnightly Rosters</Typography>
            </Breadcrumbs>

            <Grid container spacing={2} alignItems="center" style={{ marginTop: '20px' }}>
                <Grid item xs={12} sm={8}>
                    <TextField
                        label="Staff Filter"
                        variant="outlined"
                        fullWidth
                        value={searchQuery}
                        onChange={handleSearchChange}
                    />
                </Grid>
                <Grid item xs={12} sm={4}>
                    <Button
                        variant="contained"
                        color="primary"
                        fullWidth
                        onClick={handleSearchSubmit}
                    >
                        Filter
                    </Button>
                </Grid>
            </Grid>

            {/* Render the dynamic table */}
            <Table>
                {renderTableHead()}
                {loading ? (
                    <TableBody>
                        <TableRow>
                            <TableCell colSpan={6} align="center">
                                Loading...
                            </TableCell>
                        </TableRow>
                    </TableBody>
                ) : (
                    renderTableBody()
                )}
            </Table>
        </Container>
    );
}

export default StaffRoster;
