import React, { useState, useEffect } from 'react';
import { Breadcrumbs, Typography, Container, Grid, TextField, Button, MenuItem } from '@mui/material';
import { Link, useParams } from 'react-router-dom';
import { DatePicker, LocalizationProvider } from '@mui/x-date-pickers';
import { AdapterDateFns } from '@mui/x-date-pickers/AdapterDateFnsV3';

function EditLeaveRequest() {
    const { leaveRequestId } = useParams(); // Extract leaveRequestId from route params
    const [leaveType, setLeaveType] = useState('');
    const [startDate, setStartDate] = useState(null);
    const [endDate, setEndDate] = useState(null);

    // Use useEffect to fetch leave request data based on leaveRequestId
    useEffect(() => {
        const fetchLeaveRequestData = async () => {
            try {
                const response = await fetch(`https://api.example.com/leave/${leaveRequestId}`);
                if (!response.ok) {
                    throw new Error('Failed to fetch leave request data');
                }
                const data = await response.json();

                // Update state with fetched data
                setLeaveType(data.leaveType);
                setStartDate(new Date(data.startDate));
                setEndDate(new Date(data.endDate));
            } catch (error) {
                console.error('Error fetching leave request data:', error);
            }
        };

        fetchLeaveRequestData();
    }, [leaveRequestId]); // Trigger effect when leaveRequestId changes

    const handleLeaveTypeChange = (event) => {
        setLeaveType(event.target.value);
    };

    const handleStartDateChange = (date) => {
        setStartDate(date);
    };

    const handleEndDateChange = (date) => {
        setEndDate(date);
    };

    const handleSubmit = () => {
        // Implement submit logic here (e.g., API call to update leave request)
        console.log('Submitting edited leave request:', { leaveType, startDate, endDate });
        // Redirect to leave request page after submission (adjust path as needed)
        // history.push('/staff/leave');
    };

    return (
        <Container>
            <Typography variant="h4" gutterBottom>
                Edit Leave Request
            </Typography>
            <Breadcrumbs aria-label="breadcrumb">
                <Link underline="hover" color="inherit" to="/staff">
                    Staff Portal
                </Link>
                <Link underline="hover" color="inherit" to="/staff/leave">
                    Leave Request
                </Link>
                <Typography color="text.primary">Edit Request</Typography>
            </Breadcrumbs>

            <Grid container spacing={2}>
                {/* Leave Type dropdown */}
                <Grid item xs={12} sm={4}>
                    <TextField
                        select
                        fullWidth
                        label="Leave Type"
                        value={leaveType}
                        onChange={handleLeaveTypeChange}
                    >
                        <MenuItem value="Vacation">Vacation</MenuItem>
                        <MenuItem value="Sick Leave">Sick Leave</MenuItem>
                        {/* Add more leave type options as needed */}
                    </TextField>
                </Grid>

                {/* Start Date picker */}
                <Grid item xs={12} sm={4}>
                    <LocalizationProvider dateAdapter={AdapterDateFns}>
                        <DatePicker
                            label="Start Date"
                            value={startDate}
                            onChange={handleStartDateChange}
                            renderInput={(params) => <TextField {...params} fullWidth />}
                        />
                    </LocalizationProvider>
                </Grid>

                {/* End Date picker */}
                <Grid item xs={12} sm={4}>
                    <LocalizationProvider dateAdapter={AdapterDateFns}>
                        <DatePicker
                            label="End Date"
                            value={endDate}
                            onChange={handleEndDateChange}
                            renderInput={(params) => <TextField {...params} fullWidth />}
                        />
                    </LocalizationProvider>
                </Grid>

                {/* Cancel and Submit buttons */}
                <Grid item xs={6} lg={1}>
                    <Button component={Link} to="/staff/leave" variant="contained" color="secondary">
                        Cancel
                    </Button>
                </Grid>
                <Grid item xs={6} lg={1}>
                    <Button variant="contained" color="primary" onClick={handleSubmit}>
                        Submit
                    </Button>
                </Grid>
            </Grid>
        </Container>
    );
}

export default EditLeaveRequest;
