import React, { useState } from 'react';
import { Grid, TextField, Button, Container, Typography, Card, CardContent } from '@mui/material';
import { Link } from "react-router-dom"

const SignIn = () => {
    const [staffId, setStaffId] = useState('');
    const [password, setPassword] = useState('');

    const handleStaffIdChange = (e) => {
        setStaffId(e.target.value);
    };

    const handlePasswordChange = (e) => {
        setPassword(e.target.value);
    };

    const handleSubmit = (e) => {
        e.preventDefault();
        // Handle form submission (e.g., send data to server)
        console.log('StaffId:', staffId);
        console.log('Password:', password);
        // Add your authentication logic here
    };

    return (
        <Container maxWidth="lg">
            <Grid mt={10} container spacing={3} justifyContent="center" alignItems="center">
                {/* Image Section */}
                <Grid item xs={12} sm={6}>
                    <img src="https://via.placeholder.com/400" alt="Logo" style={{ maxWidth: '100%', height: 'auto' }} />
                </Grid>

                {/* Sign In Card Section */}
                <Grid item xs={12} sm={6}>
                    <Card elevation={3}>
                        <CardContent>
                            <Typography variant="h4" gutterBottom>
                                Sign In
                            </Typography>
                            <form onSubmit={handleSubmit}>
                                <TextField
                                    label="Staff Id"
                                    fullWidth
                                    margin="normal"
                                    variant="outlined"
                                    value={staffId}
                                    onChange={handleStaffIdChange}
                                    required
                                />
                                <TextField
                                    label="Password"
                                    type="password"
                                    fullWidth
                                    margin="normal"
                                    variant="outlined"
                                    value={password}
                                    onChange={handlePasswordChange}
                                    required
                                />
                                <Button
                                    type="submit"
                                    variant="contained"
                                    color="primary"
                                    fullWidth
                                    size="large"
                                    sx={{ mt: 2 }}
                                >
                                    Sign In
                                </Button>
                            </form>
                            <Button sx={{ mt: 3, mx: 3 }} component={Link} to="/manage/" variant="contained" color="secondary">
                                LogIn as Manager
                            </Button>
                            <Button sx={{ mt: 3, mx: 3 }} component={Link} to="/staff" variant="contained" color="secondary">
                                LogIn as Staff
                            </Button>

                        </CardContent>
                    </Card>
                </Grid>
            </Grid>
        </Container>
    );
};

export default SignIn;
